.searchBar,
.searchBar input:focus,
.searchBar input:valid  {
	width: 100%;
}
.searchBar input:focus,
.searchBar input:not(:focus) + .searchBtn:focus {
	outline: transparent;
}
.searchBar {
	margin: auto;
	padding: 1.5em;
	justify-content: center;
	max-width: 30em;
}
.searchBar input {
	background: transparent;
	border-radius: 1.5em;
	box-shadow: 0 0 0 0.4em #171717 inset;
	padding: 0.75em;
	-webkit-transform: translate(0.5em,0.5em) scale(0.5);
	        transform: translate(0.5em,0.5em) scale(0.5);
	-webkit-transform-origin: 100% 0;
	        transform-origin: 100% 0;
	-webkit-appearance: none;
	appearance: none;
}
.searchBar input::-webkit-search-decoration {
	-webkit-appearance: none;
}
.searchBar input:focus,
.searchBar input:valid {
	background: #fff;
	border-radius: 0.375em 0 0 0.375em;
	box-shadow: 0 0 0 0.1em #d9d9d9 inset;
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.searchBtn {
	background: #171717;
	border-radius: 0 0.75em 0.75em 0 / 0 1.5em 1.5em 0;
	padding: 0.75em;
	position: relative;
	-webkit-transform: translate(0.25em,0.25em) rotate(45deg) scale(0.25,0.125);
	        transform: translate(0.25em,0.25em) rotate(45deg) scale(0.25,0.125);
	-webkit-transform-origin: 0 50%;
	        transform-origin: 0 50%;
}
.searchBtn:before, 
.searchBtn:after {
	content: "";
	display: block;
	opacity: 0;
	position: absolute;
}
.searchBtn:before {
	border-radius: 50%;
	box-shadow: 0 0 0 0.2em #f1f1f1 inset;
	top: 0.75em;
	left: 0.75em;
	width: 1.2em;
	height: 1.2em;
}
.searchBtn:after {
	background: #f1f1f1;
	border-radius: 0 0.25em 0.25em 0;
	top: 51%;
	left: 51%;
	width: 0.75em;
	height: 0.25em;
	-webkit-transform: translate(0.2em,0) rotate(45deg);
	        transform: translate(0.2em,0) rotate(45deg);
	-webkit-transform-origin: 0 50%;
	        transform-origin: 0 50%;
}
.searchBtn span {
	display: inline-block;
	overflow: hidden;
	width: 1px;
	height: 1px;
}

/* Active state */
.searchBar input:focus + .searchBtn,
.searchBar input:valid + .searchBtn {
	background: #2762f3;
	border-radius: 0 0.375em 0.375em 0;
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.searchBar input:focus + .searchBtn:before, 
.searchBar input:focus + .searchBtn:after,
.searchBar input:valid + .searchBtn:before, 
.searchBar input:valid + .searchBtn:after {
	opacity: 1;
}
.searchBar input:focus + .searchBtn:hover,
.searchBar input:valid + .searchBtn:hover,
.searchBar input:valid:not(:focus) + .searchBtn:focus {
	background: #0c48db;
}
.searchBar input:focus + .searchBtn:active,
.searchBar input:valid + .searchBtn:active {
	-webkit-transform: translateY(1px);
	        transform: translateY(1px);
}

@media screen and (prefers-color-scheme: dark) {
	input {
		color: #f1f1f1;
	}
	.searchBar input {
		box-shadow: 0 0 0 0.4em #f1f1f1 inset;
	}
	.searchBar input:focus,
	.searchBar input:valid {
		background: #3d3d3d;
		box-shadow: 0 0 0 0.1em #3d3d3d inset;
	}
	.searchBtn {
		background: #f1f1f1;
	}
}
